// Functinal Imports
import cssNameSimplifier from "../../utility/css-name-simplifier";
import { useSelector } from "react-redux";
import { HeaderSliceName } from "../../store/header-slice";
import { useNavigate, Link } from "react-router-dom";

// Component Imports
import { Container, Box, Typography, Divider, Paper } from "@mui/material";

// Style Imports
import importedStyles from './Home.module.css';

const styles = cssNameSimplifier(importedStyles);

function Home(props){

    const appsList = useSelector(state => state[HeaderSliceName].apps);

    return (
        <>
            <Container maxWidth="lg" sx={{ marginTop: '20px' }} >
                <Box sx={{ bgcolor: '#262a2b', borderRadius: 1, padding: 2 }} >
                    <div>
                        <Typography align="center" variant="h4">
                            Quick Links
                        </Typography>
                    </div>
                        <Divider variant="middle" sx={{ margin: '10px 30%' }} light={true}/>

                    <div className={styles.quickLinksContainer}>
                        { appsList.map( app => (
                            app.endpoint !== '/' &&
                            <Link key={app.endpoint} to={`/${app.endpoint}`} style={{ textDecoration: 'none', color: '#fff'}}>
                                <Paper 
                                    elevation={16} 
                                    variant="elevation" 
                                    sx={{ padding: 3 }}
                                >
                                        {app.appName}
                                </Paper>
                            </Link>
                        ))}
                    </div>
                    
                </Box>
            </Container>
        </>
    )
}

export default Home;